export function getPath(name) {
  let path;
  switch (name) {
    case "home": // 首页
      path = "/home";
      break;
    case "standardinfo": // 标准查询
      path = "/standardinfo";
      break;
    case "standardmanage": // 标准管理
      path = "/standardmanage";
      break;
    case "searchinfo-course": // 课件查询
      path = "/searchinfo-course";
      break;
    case "searchinfo-report": // 报告查询
      path = "/searchinfo-report";
      break;
    case "reportsubmit": // 报告申请
      path = "/reportsubmit";
      break;
    case "reportmanage": // 报告管理
      path = "reportmanage";
      break;
    case "meetinginfo": // 会议信息
      path = "/meetinginfo";
      break;
    case "meetingmanage": // 会议管理
      path = "/meetingmanage";
      break;
    case "traintest": // 培训测试
      path = "/traintest";
      break;
    case "trainmanage-test": // 试题管理
      path = "/trainmanage-test";
      break;
    case "trainmanage-course": // 课件管理
      path = "/trainmanage-course";
      break;
    case "config-user": // 用户管理
      path = "/config-user";
      break;
    case "config-role": // 角色权限管理
      path = "/config-role";
      break;
    default:
      path = "/";
  }
  return path;
}
